import { Pipe, PipeTransform } from '@angular/core';

export type IdTypes = 'ein' | 'ssn';

export interface Identity {
  id: string;
  idType: IdTypes;
}

export interface Id extends Identity {
  [x: string]: any;
}

const format = (s = '', pattern = '', i = 0) =>
  pattern.replace(/#/g, () => s[i++] || '');

@Pipe({ name: 'TIN' })
export class TinPipe implements PipeTransform {
  transform({ id, idType = 'ein' }: Id, isTokenized = false): string {
    let tin = String(id ?? '').replace(/-/g, '');
    if (tin === '') return '-';
    if ('ssn' === idType) {
      if (isTokenized) {
        tin = `XXXXX${tin.slice(-4)}`;
      }
      tin = format(tin, '###-##-####');
    } else {
      tin = format(tin, '##-#######');
    }
    return tin;
  }
}
