import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'zipCode' })
export class ZipCodePipe implements PipeTransform {
  transform(value: string): string {
    let zip = value;
    if (zip && zip.length > 5) {
      if (zip.includes('-')) return zip;
      return `${zip.substr(0, 5)}-${zip.substr(5)}`;
    } else {
      return zip;
    }
  }
}
