import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'emptyDash' })
export class EmptyDashPipe implements PipeTransform {
  transform(value: string): string {
    if (
      value === null ||
      value === '' ||
      value === undefined ||
      value === ' '
    ) {
      return '-';
    } else return value;
  }
}
