import { Injectable } from '@angular/core';
import {
  HttpClient,
  HttpErrorResponse,
  HttpHeaders,
} from '@angular/common/http';
import { throwError, timer } from 'rxjs';
import { catchError, mergeMap, retryWhen } from 'rxjs/operators';
import { generateUUID } from '../utils/uuid';

type Methods = 'DELETE' | 'GET' | 'POST' | 'PUT';

@Injectable({ providedIn: 'root' })
export class DataService {
  private apiURL = '/api';
  private excludedStatusCodes = [400, 401, 500, 504];
  private maxRetries = 2;
  private requestIndex = 0;
  private uniqueId = generateUUID();
  headers: { [name: string]: string } = {};

  constructor(private http: HttpClient) {}

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      console.error(`Frontend error: ${error.error.message}`);
    } else {
      console.error(`Backend error: ${error.status}, ${error.error}`);
    }
    return throwError('Something bad happened; please try again later.');
  }

  api<T>(path: string, data?: any, method?: Methods) {
    if (!method) method = data ? 'POST' : 'GET';
    const xid = `${this.uniqueId}-${++this.requestIndex}`;
    const res = this.http.request<T>(method, this.apiURL + path, {
      headers: new HttpHeaders(this.headers).append('x-correlation-id', xid),
      body: data,
    });
    const { excludedStatusCodes, maxRetries } = this;
    return res.pipe(
      retryWhen(
        mergeMap((error, i) => {
          if (
            i + 1 > maxRetries ||
            excludedStatusCodes.includes(error.status)
          ) {
            return throwError(error);
          }
          return timer(1000);
        })
      ),
      catchError(this.handleError)
    );
  }

  mock<T>(path: string) {
    return this.http.get<T>(`assets/mock/${path}.json?ds`);
  }
}
