import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { OktaAuthService } from '@okta/okta-angular';
import { DataService } from '../services/data.service';

@Injectable({ providedIn: 'root' })
export class AuthService {
  private _activityId = '';
  private _state = new BehaviorSubject<boolean>(false);
  readonly state = this._state.asObservable();
  private _storageKey = 'pecos-cache';
  private _waitForState = false;

  get cache() {
    return JSON.parse(localStorage.getItem(this._storageKey) || '{}');
  }
  set cache(value) {
    localStorage.setItem(this._storageKey, JSON.stringify(value));
  }

  private _user: any = this.cache;
  get user() {
    return this._user;
  }

  constructor(
    private dataService: DataService,
    private oktaAuth: OktaAuthService,
    private router: Router
  ) {
    if (this.user.role) {
      this._waitForState = true;
      this.onProviderStateChange(true);
    }
    this.oktaAuth.$authenticationState.subscribe((value) => {
      this.onOktaAuthStateChange(value);
    });
  }

  private authAPI<T>(path = '', data?: any) {
    return this.dataService.api<T>(`/authentication/${path}`, data).toPromise();
  }

  async isAuthenticated() {
    if (this._waitForState) {
      return new Promise((resolve) => {
        const tick = () => {
          if (!this._waitForState) resolve(this._state.getValue());
          setTimeout(tick, 100);
        };
        tick();
      });
    } else {
      return this._state.getValue() || (await this.oktaAuth.isAuthenticated());
    }
  }

  loginRedirect() {
    window.location.href = '/login';
  }

  async login(o: any = {}) {
    let { cookieId, role, token, username } = o;
    if (!role) role = /^p/.test(username) ? 'Provider' : 'CMS';
    this.cache = { role, token, username, cookieId };
    this._user = { role, username };
    await this.onProviderStateChange(true);
    this.router.navigateByUrl('/');
  }

  async logout() {
    this._user = {};
    localStorage.removeItem(this._storageKey);
    if (await this.oktaAuth.isAuthenticated()) {
      await this.onOktaAuthStateChange(false);
      this.oktaAuth.signOut().catch(console.error);
    } else {
      await this.onProviderStateChange(false);
      this.loginRedirect();
    }
  }

  async onOktaAuthStateChange(isAuthenticated: boolean) {
    if (isAuthenticated) {
      await this.setAuthHeaders(true);
      this._user.role = 'CMS';
      const info = await this.oktaAuth.getUser();
      Object.assign(this._user, {
        firstName: info.given_name,
        lastName: info.family_name,
        username: info.name,
        info,
      });
      this._state.next(true);
      this.updateActivity(true);
    } else if (!this.cache.role) {
      this.updateActivity(false);
      await this.setAuthHeaders(false);
      this._user = {};
      this._state.next(false);
    }
  }

  async onProviderStateChange(isAuthenticated: boolean) {
    if (isAuthenticated) {
      await this.setAuthHeaders();
      const info = await this.authAPI<any>('loggedInUserDetails');
      const { firstName, lastName, username } = info;
      Object.assign(this._user, { firstName, lastName, username, info });
      this._state.next(true);
      this._waitForState = false;
      this.updateActivity(true);
    } else {
      this.updateActivity(false);
      await this.setAuthHeaders();
      this._state.next(false);
    }
  }

  async setAuthHeaders(isOktaAuthenticated?: boolean) {
    const { headers } = this.dataService;
    delete headers.Authorization;
    delete headers.PecosTokenType;
    const { role, token, username } = this.cache;
    switch (role) {
      case 'Provider':
        if (token) {
          headers.Authorization = `Bearer ${token.access_token}`;
          headers.PecosTokenType = 'PROVIDER_AUTH_TOKEN';
        } else {
          headers.Authorization = username;
          headers.PecosTokenType = 'FAKE_PROVIDER_AUTH_TOKEN';
        }
        break;
      case 'CMS':
        headers.Authorization = 'CMS';
        headers.PecosTokenType = 'FAKE_MAC_AUTH_TOKEN';
        break;
      default:
        if (isOktaAuthenticated) {
          const idToken = await this.oktaAuth.tokenManager.get('idToken');
          headers.Authorization = `Bearer ${idToken.value}`;
          headers.PecosTokenType = 'MAC_AUTH_TOKEN';
        }
    }
  }

  async updateActivity(login: boolean) {
    const id = this._activityId;
    try {
      if (login) {
        this._activityId = await this.authAPI<string>('loginActivity', {});
      } else if (id) {
        await this.authAPI(`logoutActivity?activityLogId=${id}`, {});
      }
    } catch (error) {}
  }
}
