import { Pipe, PipeTransform } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Pipe({
  name: 'highlightKeyword',
})
export class HighlightKeywordPipe implements PipeTransform {
  constructor(private _sanitizer: DomSanitizer) {}

  transform(list: any, searchText: any, type: any): any {
    if (!list) {
      return '';
    }
    if (
      !searchText ||
      searchText.indexOf('*') === 0 ||
      list === '-' ||
      type !== 'search'
    ) {
      return list;
    }
    const sText = searchText.trim();
    const sList = list && list.toString();
    const regex = new RegExp(sText, 'gi'); //'gi' for case insensitive and can use 'g' if you want the search to be case sensitive.
    const value = sList.replace(
      regex,
      '<span style="background-color:#FFF3BD">$&</span>'
    );
    return this._sanitizer.bypassSecurityTrustHtml(value);
  }
}
