import { Pipe, PipeTransform } from '@angular/core';

interface hasValueCallbackFunction {
  (value: any): boolean;
}

export const hasValue = (o: any, fn: hasValueCallbackFunction): boolean => {
  switch (Object.prototype.toString.call(o).charAt(8)) {
    case 'O':
      return Object.values(o).some((value) => hasValue(value, fn));
    case 'A':
      return o.some((child: any) => hasValue(child, fn));
    default:
      return fn(o);
  }
};

@Pipe({ name: 'filter' })
export class FilterPipe implements PipeTransform {
  transform(items: any, value: string): any {
    return FilterPipe.filter(items, value);
  }

  static filter(items: any[], value: string) {
    const lowerValue = value.toLowerCase();
    const testValue = (v: any) =>
      String(v ?? '')
        .toLowerCase()
        .indexOf(lowerValue) > -1;
    return items.filter((item) => hasValue(item, testValue));
  }
}
