import { Pipe, PipeTransform } from '@angular/core';

export interface FullNameLike {
  firstName?: string;
  lastName?: string;
  middleName?: string;
  suffix?: string;
  [x: string]: any;
}

export type FullNamePipePatterns = 'f m l s' | 'f mi l s' | string;

@Pipe({ name: 'fullName' })
export class FullNamePipe implements PipeTransform {
  transform(
    value: FullNameLike | null,
    pattern: FullNamePipePatterns = 'f m l s'
  ): string {
    const safeValue = value ?? {};
    const {
      firstName: f,
      lastName: l,
      middleName: m,
      middleInitial: mi = (m || '').trim().charAt(0),
      suffix = '',
    } = safeValue;
    const s = (suffix ?? '').trim().replace('N/A', '');

    const macro = pattern
      .replace(/f/g, '\u0001')
      .replace(/l/g, '\u0002')
      .replace(/mi/g, '\u0004')
      .replace(/m/g, '\u0003')
      .replace(/s/g, '\u0005');

    return macro
      .replace(/\u0001/g, f ?? '')
      .replace(/\u0002/g, l ?? '')
      .replace(/\u0003/g, m ?? '')
      .replace(/\u0004/g, mi ?? '')
      .replace(/\u0005/g, s ?? '')
      .replace(/ {2,}/g, ' ')
      .trim();
  }
}
