import { Injectable } from '@angular/core';
import { DataService } from './data.service';

import {
  CodeReferenceDTO,
  ContractorGroupDTO,
  ContractorGroupResponse,
  LineOfBusinessDTO,
  LineOfBusinessOptions,
  ReferenceByTypeResponse,
  PecosFlagReferenceDTO,
  PecosFlagsTypeResponse,
  PecosReferenceDataDTO,
  PecosReferenceDataResponse,
  PecosUserDTO,
  PecosUsersResponse,
  ProviderTypes,
  SpecialtyReferenceDTO,
  SpecialtyReferenceResponse,
} from './reference-data.models';

export const sortByKey = (key: string) => (a: any, b: any) =>
  a[key] > b[key] ? 1 : a[key] < b[key] ? -1 : 0;

@Injectable({
  providedIn: 'root',
})
export class ReferenceDataService {
  private cache: any = {};

  constructor(private dataService: DataService) {}

  private api<T>(path = '/referenceData'): Promise<T> {
    return this.dataService.api<T>(`/reference${path}`).toPromise();
  }

  async getByType(type: string): Promise<CodeReferenceDTO[]> {
    try {
      let list = this.cache[type];
      if (!list) {
        const data = await this.api<ReferenceByTypeResponse>(
          `/referenceByType/${type}`
        );
        list = data._embedded.codeReferenceDTOList;
        list.sort(sortByKey('value'));
        this.cache[type] = list;
      }
      return [...list]; // return a copy
    } catch (e) {
      return [];
    }
  }

  async getDocumentTypeByCategory(categoryType?: string): Promise<CodeReferenceDTO[]> {
    try {
      let list = this.cache[categoryType];
      if (!list) {
        const data = await this.api<CodeReferenceDTO[]>(
          `/reference/documentType/${categoryType||''}`
        );
        list = data;
        list.sort(sortByKey('code'));
        this.cache[categoryType] = list;
      }
      return [...list]; // return a copy
    } catch (e) {
      return [];
    }
  }

  getDocumentCategories() {
    return this.getByType('Document Category');
  }

  getCountries() {
    return this.getByType('Country Code');
  }

  getEvents() {
    return this.getByType('event');
  }

  getEventsByReferenceType(referenceType: string) {
    return this.getByType(`event/${referenceType}`);
  }

  async getFlagTypes(): Promise<PecosFlagReferenceDTO[]> {
    try {
      let list = this.cache.flagTypes;
      if (!list) {
        const data = await this.api<PecosFlagsTypeResponse>('/flagTypes');
        list = data._embedded.pecosFlagReferenceDTOList;
        list.sort(sortByKey('label'));
        this.cache.flagTypes = list;
      }
      return [...list];
    } catch (e) {
      return [];
    }
  }

  async getLineOfBusinessInfo(
    options: LineOfBusinessOptions
  ): Promise<LineOfBusinessDTO | null> {
    const { formType, physicianInd = '', specialtyCode = '' } = options;
    if (!formType) {
      throw new Error(`formType must be supplied.`);
    }
    const cacheKey = ['lob', ...Object.values(options)].join('-');
    try {
      let data = this.cache[cacheKey];
      if (!data) {
        const qs = `physicianInd=${physicianInd}&specialtyCode=${specialtyCode}`;
        data = await this.api<LineOfBusinessDTO>(
          `/getFormLineOfBusinessInfo/${formType}?${qs}`
        );
        this.cache[cacheKey] = data;
      }
      return data;
    } catch (e) {
      return null;
    }
  }

  async getMacGroups(): Promise<ContractorGroupDTO[]> {
    try {
      let list = this.cache.macGroups;
      if (!list) {
        const data = await this.api<ContractorGroupResponse>('/macGroups');
        list = data._embedded.contractorGroupDTOList;
        list.sort(sortByKey('value'));
        this.cache.macGroups = list;
      }
      return [...list];
    } catch (e) {
      return [];
    }
  }

  async getNameSuffixes(): Promise<{ code: string; value: string }[]> {
    try {
      let list = this.cache.nameSuffixes;
      if (!list) {
        // TODO: actually use /api if one becomes available
        list = [
          { code: 'JR', value: 'Jr.' },
          { code: 'SR', value: 'Sr.' },
          { code: 'II', value: 'II' },
          { code: 'III', value: 'III' },
          { code: 'IV', value: 'IV' },
          { code: 'N/A', value: 'N/A' },
        ];
        this.cache.nameSuffixes = list;
      }
      return [...list];
    } catch (e) {
      return [];
    }
  }

  async getPecosUsers(): Promise<PecosUserDTO[]> {
    try {
      let list = this.cache.pecosUsers;
      if (!list) {
        const data = await this.api<PecosUsersResponse>('/pecosUser');
        list = data._embedded.pecosUserDTOList;
        list.sort(sortByKey('cmsUserId'));
        this.cache.pecosUsers = list;
      }
      return [...list];
    } catch (e) {
      return [];
    }
  }

  async getSpecialties(): Promise<PecosReferenceDataDTO[]> {
    try {
      let list = this.cache.specialties;
      if (!list) {
        const data = await this.api<PecosReferenceDataResponse>(
          '/referenceData/specialties'
        );
        list = data._embedded.pecosReferenceDataDTOList;
        list.sort(sortByKey('codeDescription'));
        this.cache.specialties = list;
      }
      return [...list];
    } catch (e) {
      return [];
    }
  }

  async getSpecialtiesByFormType(
    formType: string,
    providerType: ProviderTypes = 'NA'
  ): Promise<SpecialtyReferenceDTO[]> {
    const cacheKey = `specialties-${formType}-${providerType}`;
    try {
      let list = this.cache[cacheKey];
      if (!list) {
        const data = await this.api<SpecialtyReferenceResponse>(
          `/specialtyRef/${formType}/${providerType}`
        );
        list = data._embedded.specialtyReferenceDTOList;
        list.sort(sortByKey('specialtyReferenceDesc'));
        this.cache[cacheKey] = list;
      }
      return [...list];
    } catch (e) {
      return [];
    }
  }

  async getSpecialtiesWithFormType(): Promise<SpecialtyReferenceDTO[]> {
    const cacheKey = `specialties-with-form-type`;
    try {
      let list = this.cache[cacheKey];
      if (!list) {
        const data = await this.api<SpecialtyReferenceResponse>(
          `/referenceByType/specialties`
        );
        list = data._embedded.specialtyReferenceDTOList;
        list.sort(sortByKey('specialtyReferenceDesc'));
        this.cache[cacheKey] = list;
      }
      return [...list];
    } catch (e) {
      return [];
    }
  }

  getStates() {
    return this.getByType('State Code');
  }

  getVehicleTypes() {
    return this.getByType('Vehicle Type');
  }

  async getDispositionReasons(reasonType: string): Promise<CodeReferenceDTO[]> {
    try {
      if (!this.cache.dispositionReasons) {
        this.cache.dispositionReasons = [];
      }

      let list = this.cache.dispositionReasons[reasonType];
      if (!list) {
        const data = await this.api<CodeReferenceDTO[]>(
          `/disposition-reason/${reasonType}`
        );
        list = data;
        list.sort(sortByKey('conceptReasonSubtype'));
        this.cache.dispositionReasons[reasonType] = list;
      }
      return [...list];
    } catch (e) {
      return [];
    }
  }

  async fetchRfiCorrespondenceSection(applicationEnum: string): Promise<Array<any>> {
    const cacheKey = 'rfi' + applicationEnum;
    try {
      if (!this.cache[cacheKey]) {
        this.cache[cacheKey] = [];
      }
      let list = this.cache[cacheKey];

      if (list.length === 0){
        const csData = await this.dataService.api(
          '/content/rfiCorrespondenceSections/' + applicationEnum
        ).toPromise();
        this.cache[cacheKey] = csData;
        list = csData;
      }
      return list;
    } catch (e) {
      return [];
    }
  }
}
