import { Pipe, PipeTransform } from '@angular/core';
import { TitleCasePipe } from '@angular/common';
import { ZipCodePipe } from './zip-code.pipe';

export interface AddressLike {
  addressLine1?: string;
  addressLine2?: string;
  addressStateCode?: string;
  addrLine1?: string;
  addrLine2?: string;
  city?: string;
  cityName?: string;
  stateCd?: string;
  stateCode?: string;
  street1?: string;
  street2?: string;
  zip?: string;
  zipCd?: string;
  zipCode?: string;
  zipcode?: string;
  [x: string]: any;
}

export type AddressPipePatterns = 'default' | 'usps' | string;

@Pipe({ name: 'address' })
export class AddressPipe implements PipeTransform {
  transform(
    o: AddressLike,
    isHTML = false,
    applyTitleCase = true,
    pattern: AddressPipePatterns = 'default',
    emptyValue = '-'
  ): string {
    if (!o) return emptyValue;

    let line1 = o.addressLine1 || o.addrLine1 || o.street1 || '';
    let line2 = o.addressLine2 || o.addrLine2 || o.street2 || '';
    let city = o.city || o.cityName || '';
    let state = o.state || o.stateCd || o.stateCode || o.addressStateCode || '';
    let zip = o.zip || o.zipCd || o.zipCode || o.zipcode || '';

    const all = line1 + line2 + city + state + zip;
    if (all.trim() === '') return emptyValue;

    if (zip) zip = new ZipCodePipe().transform(zip);

    if (applyTitleCase === true) {
      const titleCasePipe = new TitleCasePipe();
      line1 = titleCasePipe.transform(line1);
      line2 = titleCasePipe.transform(line2);
      city = titleCasePipe.transform(city);
    }

    const usps = pattern === 'usps';
    const lines = [line1];
    if (line2) lines.push(line2);
    if (isHTML) {
      let s = '';
      if (line1 || line2) {
        s += lines.join(usps ? ' ' : '<br>') + '<br>';
      }
      if (city) s += city;
      if (city && (state || zip)) s += ', ';
      if (state) s += state;
      if (s.length) s += '<br>';
      if (zip) s += zip;
      return `<address>${s}</address>`;
    } else {
      return [lines.join(' '), city, state, zip]
        .map((s) => s.trim())
        .filter((s) => s !== '')
        .join(', ');
    }
  }
}
